@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  font-family: 'Raleway',sans-serif;
  align-items: center;
  justify-content: center;
  display: flex;
  transition: 1s;
  background-color:rgb(44, 62, 80);
  color: rgb(44, 62, 80);
  height: 100vh;
}

#quote-box { 
  background-color: white;
  display: table;
  border-radius: 3px;
  width:450px;
  padding:40px 50px;
  position:relative;
  text-align:center;
  height:auto;
  clear:both;
  font-weight:500;
}

.buttons {
  width:450px;
  margin:auto;
  display:block;
}

button {
  transition: 1s;
  border: none;
  border-radius: 3px;
  color: white;
  outline:none;
  background-color:rgb(44, 62, 80);
}

.button1 { 
  float: right;
  padding: 8px 18px 6px 18px;
}

footer {
  width:450px;
  text-align:center;
  display:block;
  margin:15px auto 30px auto;
  font-size:0.8em;
  color:#fff;
}

a {
    font-weight:500;
    text-decoration:none;
    color:#fff;
}

.awesome { 
  text-align:center;
  display: flex;
  float: left;
}

.quote-text {
  text-align:center;
  width:450px;
  height:auto;
  clear:both;
  font-weight:500;
  font-size:1.75em;
}

.quote-author {
  width:450px;
  height:auto;
  clear:both;
  font-size:1em; 
  text-align:right;
}
